import uniIcon from "@/components/uni-icon/uni-icon.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniIcon,
		uniPopup
	},
	data(){
		return{
			shopObj:"",
			dataInfo:{},
			isShow:false,
			capitalType:1,//提现类型1为销售提现，2为用户返佣
			money:"",//提现金额
			pwd:"",//取现密码
			withdrawMoney:"",
		}
	},
	methods: {
		change(e){
			
			
			this.withdrawMoney=e.currentTarget.dataset.money||"";
			if(!this.isShow){
				if(this.withdrawMoney<100){
					uni.showToast({
						title:"可提现金额须大于或等于100",
						icon:"none"
					})
					return
				}
			}
			this.isShow=!this.isShow;
			this.pwd="";
			this.money="";
			this.capitalType=e.currentTarget.dataset.type
		},
		cashOut(){
			let that=this;
			if(that.money&&that.pwd){
				if(that.money<100){
					uni.showToast({
						title:"提现金额须大于或等于100",
						icon:"none"
					})
					return
				}
				that.$ajax({
					url:"/fund/withdrawMoney",
					data:{
						cashInPassword:that.pwd,
						marchantId:that.shopObj.merchantId,
						price:that.money,
						type:that.capitalType
					},success(d){
						let num=that.withdrawMoney-that.money;
						let m=that.money*1+that.dataInfo.hasBeenPresented*1;
						if(that.capitalType==1){
							that.dataInfo.salesRevenue=num.toFixed(2);
						}else{
							that.dataInfo.taxableAmountOfDividends=num.toFixed(2);
						}
						that.dataInfo.hasBeenPresented=m.toFixed(2);
						that.isShow=!that.isShow;
						uni.showToast({
							title:"提现申请提交成功",
							icon:"none"
						})
					}
				})
			}else{
				uni.showToast({
					title:"请输入提现的金额或密码",
					icon:"none"
				})
			}
		},
		url(e){
			uni.navigateTo({
				url:e.currentTarget.dataset.url
			})
		},
		urlBank(e){
			uni.navigateTo({
				url:e.currentTarget.dataset.url+"?type="+this.dataInfo.isBindingBankCard
			})
		}
	},
	onLoad(){
		var that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	},
	onShow(){
		var that=this
		this.$ajax({
			url:"/fund/searchMarchantFundIndex",
			methods:"POST",
			data:{
				marchantId:that.shopObj.merchantId
			},
			success(d){
				that.dataInfo=d
			}
		})
	}
}
